.skill {
    width: 300px;
    min-height: 250px;
    margin: 2%;
    background-color: lightcoral;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    overflow: hidden;
    flex-wrap: nowrap;
}

.icon {
    height: 60px;
    width: 60px;
    background-color: darkgray;
}

.span {
    display: flex;
    padding-left: 10px;
}

